html, body {
  height: 100%;
}

body {
  color: #000;
  margin: 0;
  font-family: Noto Serif, serif;
}

h1 {
  padding: 20px 50px 0;
  font-size: 20px;
  line-height: 1.6;
}

a {
  white-space: normal;
}

p {
  width: 100%;
  margin-top: 0;
  padding-left: 50px;
  font-size: 14px;
  line-height: 1.6;
}

.pre-wrap {
  white-space: pre-wrap;
}

div {
  box-sizing: border-box;
}

.flex-container {
  flex-direction: row;
  display: flex;
}

.half {
  flex: 0 50%;
}

.left {
  text-align: left;
  flex: 0 50%;
  padding-left: 50px;
}

.right {
  text-align: right;
  flex: 0 100%;
  padding-right: 50px;
}

.body-link {
  color: #f26a87;
}

.press-link {
  color: #b88c03;
}

@media only screen and (width >= 600px) {
  p {
    font-size: 28px;
  }

  .pre-wrap {
    font-size: 24px;
  }

  h1 {
    font-size: 34px;
  }

  .left {
    flex: 0 60%;
  }
}
